.inputs {
  font-family: var(--font-spaceMono);
  background-color: var(--white);
  padding: 0.48125rem;
}
@media (min-width: 90em) {
  .inputs {
    padding: 1.0625rem;
  }
}
.inputs label {
  display: flex;
  justify-content: space-between;
  color: var(--dark_grayish_cyan);
  font-weight: 700;
  font-size: 0.875rem;
  line-height: 1;
  margin-bottom: 0.625rem;
}
.inputs .error {
  display: none;
  color: var(--error);
  font-weight: 700;
  font-size: 0.875rem;
  line-height: 1;
  margin-bottom: 0;
  text-align: right;
}
.inputs legend {
  color: var(--dark_grayish_cyan);
  font-weight: 700;
  padding: 0;
  line-height: 1;
  margin-bottom: 1.1875rem;
}
.inputs__bill, .inputs__num, .inputs__tips, .inputs__custom {
  width: 100%;
  height: 2.96875rem;
  border: none;
  border-radius: 0.3125rem;
  font-size: 1.5rem;
  font-family: var(--font-spaceMono);
  font-weight: 700;
}
.inputs__bill, .inputs__num, .inputs__custom {
  background-color: var(--very_light_grayish_cyan);
  direction: rtl;
  color: var(--very_dark_cyan);
}
.inputs__custom {
  text-align: center;
  padding: 0;
}
.inputs__bill {
  background: url(../images/icon-dollar.svg) var(--very_light_grayish_cyan) no-repeat 19px center;
  padding: 0.9375rem 1.21875rem 0.9375rem 2.4375rem;
}
.inputs__num {
  display: block;
  background: url(../images/icon-person.svg) var(--very_light_grayish_cyan) no-repeat 19px center;
  padding: 0.9375rem 1.21875rem 0.9375rem 2.4375rem;
}
.inputs__tips {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}
.inputs__tips .radio_lbl {
  margin-bottom: 0;
  color: var(--white);
  font-size: 1.5rem;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: inherit;
  background-color: var(--very_dark_cyan);
}
.inputs__tips input[type=radio] {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  margin: 0;
  cursor: pointer;
}
.inputs__tips input[type=radio]:checked + .radio_lbl {
  color: var(--very_dark_cyan);
  background-color: var(--strong_cyan);
}
.inputs__tips input[type=radio]:hover + .radio_lbl {
  color: var(--very_dark_cyan);
  background-color: var(--hover);
}
.inputs__tips input[type=radio]:focus + .radio_lbl {
  border: 0.1875rem solid #02d5fa;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.results {
  display: flex;
  flex-direction: column;
  padding: 3.125rem 1.4375rem 1.4375rem 1.4375rem;
  background-color: var(--very_dark_cyan);
  border-radius: 1rem;
  width: 100%;
}
@media (min-width: 90em) {
  .results {
    padding: 3.75rem 2.5rem 2.5rem 2.5rem;
    width: 25.8125rem;
  }
}
.results__layout {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.results__layout--total {
  margin-bottom: 2.78125rem;
}
@media (min-width: 90em) {
  .results__layout--total {
    margin-bottom: 8.625rem;
  }
}
.results__layout--tipamount {
  margin-bottom: 2.78125rem;
}
@media (min-width: 90em) {
  .results__layout--tipamount {
    margin-bottom: 3.75rem;
  }
}
.results__bill {
  color: var(--strong_cyan);
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.4375rem;
}
@media (min-width: 90em) {
  .results__bill {
    font-size: 3rem;
    line-height: 2.1875rem;
  }
}
.results__title {
  display: flex;
  flex-direction: column;
}
.results__title p {
  color: var(--dark_grayish_cyan);
  margin: 0;
  font-size: 0.875rem;
  font-weight: 700;
}
.results__title h2 {
  color: var(--white);
  font-size: 1rem;
  font-weight: 700;
}
.results__resetBtn {
  width: 100%;
  height: 2.96875rem;
  border: none;
  border-radius: 0.3125rem;
  font-size: 1.5rem;
  font-family: var(--font-spaceMono);
  font-weight: 700;
  background-color: var(--strong_cyan);
  color: var(--very_dark_cyan);
}
.results__resetBtn:hover {
  background-color: var(--hover);
}

.tips_grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 0.875rem;
  border: none;
  padding: 0px;
  margin: 2.375rem 0 2.375rem 0;
}
@media (min-width: 90em) {
  .tips_grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 0.875rem;
  }
  .tips_grid .inputs__tips,
.tips_grid .inputs__custom {
    width: 117px;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}

*,
*:after,
*:before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-spaceMono);
  color: #fff;
  background-color: var(--light_grayish_cyan);
  height: 100%;
}

.hidden {
  position: absolute;
  top: -9999px;
  left: -9999px;
}

:root {
  --error: hsl(12, 72%, 61%);
  --strong_cyan: hsl(172, 67%, 45%);
  --hover: hsl(173, 61%, 77%);
  --very_dark_cyan: hsl(183, 100%, 15%);
  --dark_grayish_cyan: hsl(186, 14%, 43%);
  --grayish_cyan: hsl(184, 14%, 56%);
  --light_grayish_cyan: hsl(185, 41%, 84%);
  --very_light_grayish_cyan: hsl(189, 41%, 97%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-spaceMono: "Space Mono", monospace;
}

body {
  display: table;
  margin: 0 auto;
}

.wrapper {
  display: table-cell;
  height: 100%;
}
@media (min-width: 90em) {
  .wrapper {
    vertical-align: middle;
  }
}

.container {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--white);
  padding: 1.5rem 1.5rem 1.5rem 1.5rem;
  border-radius: 1.5625rem;
}
.container > * + * {
  margin-top: 1.4875rem;
}
@media (min-width: 90em) {
  .container {
    width: fit-content;
    max-width: 57.8125rem;
    margin: 0 auto 0 auto;
    flex-direction: row;
    padding: 2rem 2rem 2rem 2rem;
  }
  .container > * + * {
    margin-top: 0rem;
    margin-left: 1.9375rem;
  }
}

.logo {
  text-align: center;
  margin-bottom: 2.5rem;
  margin-top: 3.125rem;
}
@media (min-width: 90em) {
  .logo {
    margin-bottom: 5.5rem;
    margin-top: auto;
  }
}

h1,
h2,
h3,
p {
  margin: 0px;
  padding: 0px;
  line-height: 1.1;
}